<template>
  <div class="component" ref="componentRef">
    <div class="box">
      <v-dialog v-model="dialogDelete" max-width="500px">
        <v-card>
          <v-card-title class="text-h5">Delete</v-card-title>
          <p class="dialogContent">해당 유저를 삭제 하시겠습니까?</p>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="closeDelete"
              >Cancel</v-btn
            >
            <v-btn color="blue darken-1" text @click="deleteItemConfirm"
              >OK</v-btn
            >
            <v-spacer></v-spacer>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <div class="col">
        <div class="box rowBox">
          <div class="hyTitle">유저</div>
          <v-btn color="primary" dark @click="addItem"> New Item </v-btn>
          <v-dialog v-model="dialog" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="text-h6">{{ regTitle }}</span>
              </v-card-title>
              <v-form ref="formRef" @submit.prevent>
                <div class="regContainer">
                  <div
                    class="regBox"
                    v-for="(item, idx) in fieldItems"
                    :key="idx"
                  >
                    <!--  -->
                    <v-text-field
                      v-if="item.type === 'text'"
                      v-model="newItem[item.value]"
                      :rules="[
                        () => !!newItem[item.value] || 'This field is required',
                      ]"
                      :label="item.text"
                      :placeholder="item.text"
                      required
                    ></v-text-field>
                    <!--  -->
                    <v-autocomplete
                      v-if="item.type === 'select'"
                      v-model="newItem[item.value]"
                      :rules="[
                        () => !!newItem[item.value] || 'This field is required',
                      ]"
                      :items="selectItems"
                      :label="item.text"
                      placeholder="Select..."
                      required
                    ></v-autocomplete>
                  </div>
                </div>
              </v-form>

              <!-- <v-divider class="mt-12"></v-divider> -->
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">
                  Cancel
                </v-btn>
                <v-btn color="blue darken-1" text @click="save"> Save </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <div class="spacer"></div>
          <v-text-field
            v-model="contentTableSearch"
            append-icon="mdi-magnify"
            label="검색"
            single-line
            hide-details
          ></v-text-field>
        </div>
        <div class="box">
          <v-data-table
            fixed-header
            :search="contentTableSearch"
            :headers="contentHeaders"
            :items="contentTableList"
            :items-per-page="-1"
            item-key="name"
          >
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon small class="mr-2" @click="editItem(item)">
                mdi-pencil
              </v-icon>
              <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
            </template>
          </v-data-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  ref,
  // watch,
  computed,
  nextTick,
  reactive,
} from "vue";
import { getRequest, postRequest, deleteRequest } from "@/services/request.js";

export default {
  name: "RegComponent",
  components: {},
  props: {
    obj: Object,
  },

  setup(props) {
    const { contentHeaders, masterApi } = props.obj;

    //
    const formRef = ref();
    const componentRef = ref();

    //
    const contentTableSearch = ref("");
    const contentTableList = ref([]);

    getRequest([masterApi, `${masterApi}s`], "", ({ data }) => {
      contentTableList.value = data;
    });

    const selectItems = ref([1, 2, 3]);
    const defaultRegItem = {};
    const fieldItems = contentHeaders
      .filter((item) => {
        if (Object.prototype.hasOwnProperty.call(item, "type")) {
          if (item.type === "select") {
            getRequest(item.api, "", ({ data }) => {
              item.data = data;
            });
          }
          return true;
        } else {
          return false;
        }
      })
      .map((item) => {
        defaultRegItem[item.value] = "";
        return item;
      });

    const itemIdx = ref(-1);
    const newItem = reactive(Object.assign({}, defaultRegItem));

    //
    const dialog = ref(false);
    const dialogDelete = ref(false);
    const defaultItem = ref(Object.assign({}, defaultRegItem));

    const addItem = () => {
      dialog.value = true;
    };

    const editItem = (item) => {
      itemIdx.value = contentTableList.value.indexOf(item);
      Object.assign(newItem, item);
      dialog.value = true;
    };

    const deleteItem = (item) => {
      itemIdx.value = contentTableList.value.indexOf(item);
      Object.assign(newItem, item);
      dialogDelete.value = true;
    };

    const deleteItemConfirm = () => {
      const idx = itemIdx.value;
      const item = Object.assign({}, contentTableList.value[itemIdx.value]);
      deleteRequest(
        [
          masterApi,
          `delete${masterApi.charAt(0).toUpperCase() + masterApi.slice(1)}`,
        ],
        item,
        (res) => {
          if (res.state === "00") {
            contentTableList.value.splice(idx, 1);
          } else {
            console.log("???");
          }
        }
      );
      closeDelete();
    };
    const close = () => {
      dialog.value = false;
      nextTick(() => {
        itemIdx.value = -1;
        formRef.value.reset();
        Object.assign(newItem, defaultRegItem);
      });
    };

    const closeDelete = () => {
      dialogDelete.value = false;
      nextTick(() => {
        itemIdx.value = -1;
        Object.assign(newItem, defaultRegItem);
      });
    };

    const save = () => {
      const item = Object.assign({}, newItem);
      const idx = itemIdx.value;

      if (idx > -1) {
        // edit
        postRequest(
          [
            masterApi,
            `update${masterApi.charAt(0).toUpperCase() + masterApi.slice(1)}`,
          ],
          item,
          (res) => {
            Object.assign(contentTableList.value[idx], res.data);
          }
        );
      } else {
        console.log([
          masterApi,
          `create${masterApi.charAt(0).toUpperCase() + masterApi.slice(1)}`,
        ]);

        // save
        postRequest(
          [
            masterApi,
            `create${masterApi.charAt(0).toUpperCase() + masterApi.slice(1)}`,
          ],
          item,
          (res) => {
            contentTableList.value.push(res.data);
          }
        );
      }
      close();
    };

    const regTitle = computed(() => {
      return itemIdx.value === -1 ? "New Item" : "Edit Item";
    });

    return {
      componentRef,
      formRef,
      //
      itemIdx,
      newItem,
      //
      regTitle,
      fieldItems,

      addItem,
      selectItems,
      dialog,
      dialogDelete,
      contentHeaders,
      contentTableList,
      defaultItem,
      editItem,
      deleteItem,
      deleteItemConfirm,
      close,
      closeDelete,
      save,
      contentTableSearch,
    };
  },
};
</script>

<style></style>
