<template>
  <div class="component">
    <div class="box">
      <div class="col">
        <div class="box rowBox">
          <!--  -->
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">Delete</v-card-title>
              <p class="dialogContent">외부태그를 삭제 하시겠습니까?</p>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >Cancel</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  >OK</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialog" max-width="1200px">
            <v-card class="full">
              <v-card-title>
                <span class="text-h6">Insert Item</span>
              </v-card-title>
              <div class="rowBox">
                <v-text-field
                  v-model="dialogTableSearch"
                  append-icon="mdi-magnify"
                  label="검색"
                  single-line
                  hide-details
                ></v-text-field>
              </div>
              <v-container>
                <v-data-table
                  fixed-header
                  :search="dialogTableSearch"
                  :headers="popupHeaders"
                  :items="popupTableList"
                  :items-per-page="-1"
                  item-key="name"
                >
                  <template v-slot:[`item.actions`]="{ item }">
                    <div class="actionsBox">
                      <v-icon small @click="pushItem(item)">
                        mdi-playlist-plus
                      </v-icon>
                    </div>
                  </template>
                </v-data-table>
              </v-container>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">
                  Cancel
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <!--  -->
          <div class="hyTitle">내부태그</div>
          <v-select
            v-model="selected"
            :items="selectBox"
            :item-text="(item) => item.shipname"
            :item-value="(item) => item.shipid"
            menu-props="auto"
            label="선박"
            hide-details
            prepend-icon="mdi-ferry"
            single-line
          >
          </v-select>
          <div class="spacer"></div>
          <v-btn depressed color="primary" @click="saveItems()">저장</v-btn>
        </div>
        <div class="box">
          <v-data-table
            class="spliceTable"
            fixed-header
            :headers="contentHeaders"
            :items="contentTableList"
            :items-per-page="-1"
            item-key="name"
          >
            <template v-slot:[`item.actions`]="{ item }">
              <div class="actionsBox">
                <v-icon small class="mr-2" @click="insertItem(item)">
                  mdi-playlist-check
                </v-icon>
                <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
              </div>
            </template>
          </v-data-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  ref,
  // watch,
  nextTick,
} from "vue";
// import { useAuthStore } from "@/stories/mainStore.js";
import {
  getRequest,
  postRequest,
  // deleteRequest
} from "@/services/request.js";

export default {
  name: "UserComponent",
  components: {},
  props: {
    obj: Object,
  },

  setup(props) {
    const { contentComboApi, contentApi, popupApi } = props.obj;

    const saveMap = new Map();

    //
    const dialog = ref(false);
    const dialogDelete = ref(false);
    const dialogTableSearch = ref("");

    //
    const selectBox = ref([]);
    const selected = ref(null);

    const contentTableList = ref([]);
    const contentHeaders = props.obj.contentHeaders;
    const popupTableList = ref([]);
    const popupHeaders = props.obj.popupHeaders;

    const editedIndex = ref(-1);
    const defaultItem = ref({
      no: 0,
      a: "-",
      b: "-",
      c: "-",
      d: "-",
      e: "-",
      f: "-",
    });
    const editedItem = ref({
      no: 0,
      a: "-",
      b: "-",
      c: "-",
      d: "-",
      e: "-",
      f: "-",
    });
    const deleteProps = ["d", "e", "f"];

    getRequest(contentComboApi, "", ({ data }) => {
      selectBox.value = Array.from(data);
    });
    getRequest(contentApi, "", ({ data }) => {
      contentTableList.value = Array.from(data);
    });
    getRequest(popupApi, "", ({ data }) => {
      popupTableList.value = Array.from(data);
    });

    const insertItem = (item) => {
      editedIndex.value = contentTableList.value.indexOf(item);
      editedItem.value = Object.assign({}, item);
      dialog.value = true;
    };
    const pushItem = (item) => {
      const _editedItem = editedItem.value;
      for (const i in item) {
        _editedItem[i] = item[i];
      }

      // save
      const saveObj = Object.assign(
        contentTableList.value[editedIndex.value],
        editedItem.value
      );

      saveMap.set(saveObj.no, Object.assign({}, saveObj));

      //
      close();
    };
    const deleteItem = (item) => {
      editedIndex.value = contentTableList.value.indexOf(item);
      editedItem.value = Object.assign({}, item);
      dialogDelete.value = true;
    };
    const deleteItemConfirm = () => {
      const _editedItem = editedItem.value;
      deleteProps.map((key) => {
        _editedItem[key] = "";
      });

      const saveObj = Object.assign(
        contentTableList.value[editedIndex.value],
        editedItem.value
      );
      saveMap.set(saveObj.no, Object.assign({}, saveObj));
      closeDelete();
    };
    const close = () => {
      dialog.value = false;
      nextTick(() => {
        editedItem.value = Object.assign({}, defaultItem.value);
        editedIndex.value = -1;
      });
    };
    const closeDelete = () => {
      dialogDelete.value = false;
      nextTick(() => {
        editedItem.value = Object.assign({}, defaultItem.value);
        editedIndex.value = -1;
      });
    };

    const saveItems = () => {
      const tags = Array.from(saveMap.values());
      postRequest(["tag", "saveTags"], tags, (result) => {
        console.log(result);
        alert("데이터 저장이 완료 되었습니다.");
      });

      saveMap.clear();
    };

    // watch(dialog, (val) => {
    //   val || close();
    // });

    // watch(dialogDelete, (val) => {
    //   val || closeDelete();
    // });

    // watch(selected, (id) => {
    //   getContentTable(id);
    // });

    return {
      popupHeaders,
      popupTableList,

      dialog,
      dialogDelete,
      contentHeaders,
      contentTableList,
      editedIndex,
      editedItem,
      pushItem,
      defaultItem,
      //
      insertItem,
      deleteItem,
      deleteItemConfirm,
      close,
      closeDelete,

      saveItems,

      dialogTableSearch,
      //
      selectBox,
      selected,
    };
  },
};
</script>

<style></style>
