<template>
  <div class="component" ref="componentRef">
    <div class="box">
      <v-dialog v-model="contentDialogDelete" max-width="500px">
        <v-card>
          <v-card-title class="text-h5">Delete</v-card-title>
          <p class="dialogContent">해당 메뉴를 삭제 하시겠습니까?</p>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="closeDelete"
              >Cancel</v-btn
            >
            <v-btn color="blue darken-1" text @click="deleteItemConfirm"
              >OK</v-btn
            >
            <v-spacer></v-spacer>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <div class="col">
        <div class="box rowBox">
          <div class="hyTitle">유저</div>
          <v-btn color="primary" dark @click="addItem"> New Item </v-btn>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark @click="saveItems"> Save </v-btn>
          <v-dialog v-model="contentDialog" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="text-h6">New Item</span>
              </v-card-title>
              <v-form ref="formRef" @submit.prevent>
                <div class="regContainer">
                  <div
                    class="regBox"
                    v-for="(item, idx) in fieldItems"
                    :key="idx"
                  >
                    <!--  -->
                    <v-text-field
                      v-if="item.type === 'text'"
                      v-model="newItem[item.value]"
                      :rules="[
                        () => !!newItem[item.value] || 'This field is required',
                      ]"
                      :label="item.text"
                      :placeholder="item.text"
                      required
                    ></v-text-field>
                    <!--  -->
                    <v-autocomplete
                      v-if="item.type === 'select'"
                      v-model="newItem[item.value]"
                      :rules="[
                        () => !!newItem[item.value] || 'This field is required',
                      ]"
                      :items="selectItems"
                      :label="item.text"
                      placeholder="Select..."
                      required
                    ></v-autocomplete>
                  </div>
                </div>
              </v-form>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeNewItem">
                  Cancel
                </v-btn>
                <v-btn color="blue darken-1" text @click="saveNewItem">
                  Save
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </div>
        <div class="box">
          <v-data-table
            fixed-header
            :headers="contentHeaders"
            :items="contentTableList"
            :items-per-page="-1"
            item-key="name"
          >
            <template v-slot:[`item.actions`]="{ item, index }">
              <div class="actionsBox">
                <v-icon
                  small
                  class="mr-1"
                  @click="up(contentTableList, item)"
                  v-if="index !== 0"
                >
                  mdi-arrow-up-thin
                </v-icon>
                <v-icon
                  small
                  class="mr-2"
                  @click="down(contentTableList, item)"
                  v-if="contentTableList.length - 1 !== index"
                >
                  mdi-arrow-down-thin
                </v-icon>
                <v-icon small @click="deleteItem(contentTableList, item)">
                  mdi-delete
                </v-icon>
              </div>
            </template>
            <template v-slot:[`item.idx`]="{ index }">
              {{ index + 1 }}
            </template>
            <template v-slot:[`item.name`]="{ item }">
              <v-text-field
                v-model="item.name"
                dense
                outlined
                placeholder="menu"
                hide-details="auto"
                class="caption"
                style="width: 150px; margin: 0 auto"
              ></v-text-field>
            </template>

            <template v-slot:[`item.type`]="{ item }">
              <v-select
                v-model="item.type"
                :items="selectItems"
                placeholder="type"
                dense
                outlined
                class="caption"
                hide-details="auto"
                style="width: 150px; margin: 0 auto"
              ></v-select>
            </template>

            <template v-slot:[`item.tabs`]="{ item }">
              <ul class="menuList">
                <li v-for="(tab, index) in item.tabs" :key="index">
                  <span>Menu.</span>
                  <v-text-field
                    v-model="tab.name"
                    dense
                    placeholder="menu"
                    hide-details="auto"
                    class="caption"
                    style="width: 150px; margin: 0 auto"
                  >
                  </v-text-field>
                  <span>Type.</span>
                  <v-select
                    v-model="tab.type"
                    :items="selectItems"
                    placeholder="type"
                    dense
                    class="caption"
                    hide-details="auto"
                    style="width: 150px; margin: 0 auto"
                  ></v-select>
                  <div class="actionController">
                    <v-icon
                      small
                      @click="addTab(item.tabs, tab)"
                      v-if="item.tabs.length - 1 === index"
                    >
                      mdi-plus
                    </v-icon>
                    <v-icon
                      small
                      class="mr-1"
                      @click="up(item.tabs, tab)"
                      v-if="index !== 0"
                    >
                      mdi-arrow-up-thin
                    </v-icon>
                    <v-icon
                      small
                      class="mr-2"
                      @click="down(item.tabs, tab)"
                      v-if="item.tabs.length - 1 !== index"
                    >
                      mdi-arrow-down-thin
                    </v-icon>
                    <v-icon
                      small
                      v-if="item.tabs.length > 1"
                      @click="deleteItem(item.tabs, tab)"
                    >
                      mdi-delete
                    </v-icon>
                  </div>
                </li>
              </ul>
            </template>
          </v-data-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, nextTick, reactive } from "vue";
import { getRequest, postRequest } from "@/services/request.js";

export default {
  name: "RegComponent",
  components: {},
  props: {
    obj: Object,
  },

  setup(props) {
    const { contentHeaders, tabHeaders, contentApi } = props.obj;

    let targetList = null;
    let targetItem = null;

    //
    const formRef = ref();
    const componentRef = ref();

    //
    const contentTableList = ref([]);
    getRequest(contentApi, "", (list) => {
      console.log(list);

      contentTableList.value = list;
    });

    const tabTableList = ref([]);
    const selectItems = ref([]);
    const defaultRegItem = {};
    const fieldItems = contentHeaders
      .filter((item) => {
        if (Object.prototype.hasOwnProperty.call(item, "type")) {
          if (item.type === "select") {
            getRequest(item.api, "", ({ data: items }) => {
              item.items = items;
              selectItems.value = items;
            });
          }
          return true;
        } else {
          return false;
        }
      })
      .map((item) => {
        if (item.value === "tabs") {
          defaultRegItem[item.value] = [
            {
              name: "",
              type: "all",
            },
          ];
        } else {
          defaultRegItem[item.value] = "";
        }
        return item;
      });

    const newItem = reactive(Object.assign({}, defaultRegItem));

    //
    const contentDialog = ref(false);
    const contentDialogDelete = ref(false);

    const up = (list, item) => {
      const idx = list.indexOf(item);
      list.splice(idx, 1);
      list.splice(idx - 1, 0, item);
    };
    const down = (list, item) => {
      const idx = list.indexOf(item);
      list.splice(idx, 1);
      list.splice(idx + 1, 0, item);
    };

    const addItem = () => {
      contentDialog.value = true;
    };
    const addTab = (list) => {
      list.push({
        name: "",
        type: "all",
      });
    };
    const deleteItem = (list, item) => {
      targetList = list;
      targetItem = item;
      contentDialogDelete.value = true;
    };
    const deleteItemConfirm = () => {
      const idx = targetList.indexOf(targetItem);
      targetList.splice(idx, 1);
      closeDelete();
      // nextTick(() => {
      // });
    };
    const closeDelete = () => {
      contentDialogDelete.value = false;
      nextTick(() => {
        targetList = null;
        targetItem = null;
      });
    };
    const saveNewItem = () => {
      contentTableList.value.push(Object.assign({}, newItem));
      closeNewItem();
    };
    const closeNewItem = () => {
      contentDialog.value = false;
      nextTick(() => {
        formRef.value.reset();
        Object.assign(newItem, defaultRegItem);
      });
    };

    const saveItems = () => {
      console.log(Array.from(contentTableList.value));
      postRequest(
        ["menu", "saveMenus"],
        Array.from(contentTableList.value),
        (result) => {
          console.log(result);
          alert("데이터 저장이 완료 되었습니다.");
        }
      );
    };

    return {
      componentRef,
      formRef,
      //
      newItem,
      //
      fieldItems,

      up,
      down,
      addTab,

      addItem,
      saveItems,

      selectItems,
      contentDialog,
      contentDialogDelete,
      contentHeaders,
      contentTableList,
      tabHeaders,
      tabTableList,
      deleteItem,
      deleteItemConfirm,
      closeNewItem,
      closeDelete,
      saveNewItem,
    };
  },
};
</script>

<style></style>
